// =============================================================================
// Webhook Handler Utilities
// =============================================================================
// Standardized utilities for handling incoming webhooks with:
// - Raw body parsing
// - Signature verification
// - Idempotent processing
// - Structured error logging
//
// This module provides a consistent pattern for all webhook endpoints.

import { NextRequest, NextResponse } from 'next/server';
import { createLogger, type LogContext } from './logger';
import { getOrCreateRequestId } from './request-id';

const logger = createLogger({ component: 'WebhookHandler' });

// -----------------------------------------------------------------------------
// Types
// -----------------------------------------------------------------------------

/**
 * Result of reading raw body from request
 */
export type RawBodyResult = {
  success: true;
  rawBody: string;
  requestId: string;
} | {
  success: false;
  error: string;
  response: NextResponse;
  requestId: string;
}

/**
 * Webhook verification options
 */
export interface WebhookVerifyOptions {
  /** Raw body string */
  rawBody: string;
  /** Request headers */
  headers: Headers;
  /** Header name for signature */
  signatureHeader: string;
  /** Header name for timestamp */
  timestampHeader: string;
  /** Secret for verification */
  secret: string;
  /** Max age tolerance in seconds (default: 300 = 5 minutes) */
  toleranceSeconds?: number;
  /** Request ID for logging */
  requestId: string;
  /** Component name for logging */
  component: string;
}

/**
 * Webhook verification result
 */
export type WebhookVerifyResult<T = unknown> = {
  success: true;
  event: T;
} | {
  success: false;
  error: string;
  response: NextResponse;
}

/**
 * Idempotency check options
 */
export interface IdempotencyCheckOptions {
  /** Event ID to check */
  eventId: string;
  /** Function to check if event was processed */
  isProcessed: (eventId: string) => Promise<boolean>;
  /** Request ID for logging */
  requestId: string;
  /** Component name for logging */
  component: string;
}

/**
 * Idempotency check result
 */
export type IdempotencyCheckResult = {
  success: true;
  isNewEvent: boolean;
  response?: NextResponse; // Present if duplicate
} | {
  success: false;
  error: string;
  response: NextResponse;
}

/**
 * Webhook handler context
 */
export interface WebhookContext {
  requestId: string;
  rawBody: string;
  component: string;
  logger: ReturnType<typeof createLogger>;
}

// -----------------------------------------------------------------------------
// Raw Body Handling
// -----------------------------------------------------------------------------

/**
 * Read raw body from Next.js App Router request
 * 
 * In App Router, we use request.text() to get the raw body.
 * This is different from Pages Router which uses bodyParser: false.
 */
export async function readRawBody(
  request: NextRequest,
  component: string
): Promise<RawBodyResult> {
  const requestId = getOrCreateRequestId(request);
  const componentLogger = createLogger({ component });

  try {
    const rawBody = await request.text();

    if (!rawBody || rawBody.trim() === '') {
      componentLogger.warn(
        { requestId },
        'Empty webhook body received'
      );
      return {
        success: false,
        error: 'Empty request body',
        requestId,
        response: NextResponse.json(
          { 
            error: 'Empty request body',
            code: 'EMPTY_BODY',
            requestId,
          },
          { status: 400 }
        ),
      };
    }

    return {
      success: true,
      rawBody,
      requestId,
    };
  } catch (error) {
    const errorMessage = error instanceof Error ? error.message : 'Failed to read body';
    componentLogger.error(
      { requestId, error: errorMessage },
      'Failed to read webhook body'
    );
    return {
      success: false,
      error: errorMessage,
      requestId,
      response: NextResponse.json(
        { 
          error: 'Failed to read request body',
          code: 'BODY_READ_ERROR',
          requestId,
        },
        { status: 400 }
      ),
    };
  }
}

// -----------------------------------------------------------------------------
// Signature Header Extraction
// -----------------------------------------------------------------------------

/**
 * Extract and validate signature headers
 */
export function extractSignatureHeaders(
  headers: Headers,
  signatureHeader: string,
  timestampHeader: string,
  requestId: string,
  component: string
): {
  success: true;
  signature: string;
  timestamp: number;
} | {
  success: false;
  error: string;
  response: NextResponse;
} {
  const componentLogger = createLogger({ component });
  
  const signature = headers.get(signatureHeader);
  const timestampStr = headers.get(timestampHeader);

  if (!signature) {
    componentLogger.warn(
      { requestId, header: signatureHeader },
      'Missing signature header'
    );
    return {
      success: false,
      error: `Missing ${signatureHeader} header`,
      response: NextResponse.json(
        {
          error: 'Missing signature header',
          code: 'MISSING_SIGNATURE',
          requestId,
        },
        { status: 401 }
      ),
    };
  }

  if (!timestampStr) {
    componentLogger.warn(
      { requestId, header: timestampHeader },
      'Missing timestamp header'
    );
    return {
      success: false,
      error: `Missing ${timestampHeader} header`,
      response: NextResponse.json(
        {
          error: 'Missing timestamp header',
          code: 'MISSING_TIMESTAMP',
          requestId,
        },
        { status: 401 }
      ),
    };
  }

  const timestamp = parseInt(timestampStr, 10);
  if (isNaN(timestamp)) {
    componentLogger.warn(
      { requestId, timestampStr },
      'Invalid timestamp header value'
    );
    return {
      success: false,
      error: 'Invalid timestamp header',
      response: NextResponse.json(
        {
          error: 'Invalid timestamp header',
          code: 'INVALID_TIMESTAMP',
          requestId,
        },
        { status: 400 }
      ),
    };
  }

  return {
    success: true,
    signature,
    timestamp,
  };
}

// -----------------------------------------------------------------------------
// Idempotency Check
// -----------------------------------------------------------------------------

/**
 * Check if webhook event has already been processed
 */
export async function checkIdempotency(
  options: IdempotencyCheckOptions
): Promise<IdempotencyCheckResult> {
  const { eventId, isProcessed, requestId, component } = options;
  const componentLogger = createLogger({ component });

  try {
    const alreadyProcessed = await isProcessed(eventId);

    if (alreadyProcessed) {
      componentLogger.info(
        { requestId, eventId },
        'Webhook event already processed, acknowledging duplicate'
      );
      return {
        success: true,
        isNewEvent: false,
        response: NextResponse.json({
          received: true,
          duplicate: true,
          requestId,
        }),
      };
    }

    return {
      success: true,
      isNewEvent: true,
    };
  } catch (error) {
    const errorMessage = error instanceof Error ? error.message : 'Idempotency check failed';
    componentLogger.error(
      { requestId, eventId, error: errorMessage },
      'Idempotency check failed'
    );
    return {
      success: false,
      error: errorMessage,
      response: NextResponse.json(
        {
          error: 'Idempotency check failed',
          code: 'IDEMPOTENCY_ERROR',
          requestId,
        },
        { status: 500 }
      ),
    };
  }
}

// -----------------------------------------------------------------------------
// Standard Response Helpers
// -----------------------------------------------------------------------------

/**
 * Create success response for processed webhook
 */
export function createWebhookSuccessResponse(
  requestId: string,
  eventId?: string,
  extra?: Record<string, unknown>
): NextResponse {
  return NextResponse.json({
    received: true,
    requestId,
    ...(eventId && { eventId }),
    ...extra,
  });
}

/**
 * Create error response for webhook processing failure
 */
export function createWebhookErrorResponse(
  code: string,
  message: string,
  requestId: string,
  status: number = 500,
  extra?: Record<string, unknown>
): NextResponse {
  return NextResponse.json(
    {
      error: message,
      code,
      requestId,
      ...extra,
    },
    { status }
  );
}

/**
 * Create response for partial processing (received but not fully processed)
 */
export function createWebhookPartialResponse(
  requestId: string,
  errorMessage: string,
  eventId?: string
): NextResponse {
  return NextResponse.json({
    received: true,
    processed: false,
    error: errorMessage,
    requestId,
    ...(eventId && { eventId }),
  });
}

// -----------------------------------------------------------------------------
// Logging Helpers
// -----------------------------------------------------------------------------

/**
 * Log webhook receipt
 */
export function logWebhookReceived(
  component: string,
  requestId: string,
  eventType: string,
  eventId: string,
  extra?: Record<string, unknown>
): void {
  const componentLogger = createLogger({ component });
  componentLogger.info(
    { requestId, eventId, eventType, ...extra },
    'Webhook received and verified'
  );
}

/**
 * Log webhook processing success
 */
export function logWebhookProcessed(
  component: string,
  requestId: string,
  eventType: string,
  eventId: string,
  extra?: Record<string, unknown>
): void {
  const componentLogger = createLogger({ component });
  componentLogger.info(
    { requestId, eventId, eventType, ...extra },
    'Webhook processed successfully'
  );
}

/**
 * Log webhook processing failure
 */
export function logWebhookFailed(
  component: string,
  requestId: string,
  eventType: string,
  eventId: string,
  error: string,
  extra?: Record<string, unknown>
): void {
  const componentLogger = createLogger({ component });
  componentLogger.error(
    { requestId, eventId, eventType, error, ...extra },
    'Webhook processing failed'
  );
}

/**
 * Log unhandled event type
 */
export function logUnhandledEventType(
  component: string,
  requestId: string,
  eventType: string,
  eventId: string
): void {
  const componentLogger = createLogger({ component });
  componentLogger.info(
    { requestId, eventId, eventType },
    'Received unhandled webhook event type'
  );
}

