// =============================================================================
// POST /api/webhooks/smf - SMF Webhook Handler
// =============================================================================
// Handles incoming webhooks from SMF with:
// - Raw body parsing (App Router compatible)
// - Signature verification via @smf/sdk
// - Idempotent event processing
// - Structured error logging
//
// SECURITY: All webhooks are verified using HMAC signature before processing.
// IDEMPOTENCY: Events are tracked in smf_webhook_events to prevent double-processing.

import { NextRequest, NextResponse } from 'next/server';
import { verifyWebhookSignature, InvalidSignatureError } from '@smf/sdk';
import { getWebhookSecret } from '@/payments/smfClient';
import {
  isWebhookEventProcessed,
  createWebhookEvent,
  markWebhookEventProcessed,
  updateChargeStatus,
  getChargeBySmfId,
  getRefundBySmfId,
  updateRefundStatus,
  getPayoutBySmfId,
  updatePayoutStatus,
} from '@/db/smf-payments';
import { createLogger } from '@/lib/logger';
import {
  readRawBody,
  extractSignatureHeaders,
  checkIdempotency,
  createWebhookSuccessResponse,
  createWebhookErrorResponse,
  createWebhookPartialResponse,
  logWebhookReceived,
  logWebhookProcessed,
  logWebhookFailed,
  logUnhandledEventType,
} from '@/lib/webhook-handler';

const COMPONENT = 'SMFWebhook';
const logger = createLogger({ component: COMPONENT });

// -----------------------------------------------------------------------------
// Webhook Event Types
// -----------------------------------------------------------------------------

interface SmfWebhookEvent {
  id: string;
  type: string;
  createdAt: string;
  data: {
    id: string;
    object: string;
    status?: string;
    amount?: number;
    currency?: string;
    chargeId?: string; // For refunds
    error?: {
      code?: string;
      message?: string;
    };
    metadata?: Record<string, unknown>;
    [key: string]: unknown;
  };
}

// -----------------------------------------------------------------------------
// Event Handlers
// -----------------------------------------------------------------------------

/**
 * Handle payment.succeeded event
 * Updates charge status to 'succeeded' in database
 */
async function handlePaymentSucceeded(
  event: SmfWebhookEvent,
  requestId: string
): Promise<void> {
  const chargeId = event.data.id;

  logger.info(
    { requestId, chargeId, eventId: event.id },
    'Processing payment.succeeded event'
  );

  const charge = await getChargeBySmfId(chargeId);

  if (charge) {
    if (charge.status !== 'succeeded') {
      await updateChargeStatus(charge.id, 'succeeded');
      logger.info(
        { requestId, chargeId, orderId: charge.order_id },
        'Charge marked as succeeded'
      );
    } else {
      logger.debug(
        { requestId, chargeId },
        'Charge already succeeded, skipping update'
      );
    }
  } else {
    logger.warn(
      { requestId, chargeId, eventId: event.id },
      'Received payment.succeeded for unknown charge'
    );
  }
}

/**
 * Handle payment.failed event
 * Updates charge status to 'failed' with error details
 */
async function handlePaymentFailed(
  event: SmfWebhookEvent,
  requestId: string
): Promise<void> {
  const chargeId = event.data.id;
  const errorCode = event.data.error?.code;
  const errorMessage = event.data.error?.message;

  logger.info(
    { requestId, chargeId, eventId: event.id, errorCode },
    'Processing payment.failed event'
  );

  const charge = await getChargeBySmfId(chargeId);

  if (charge) {
    if (charge.status !== 'failed') {
      await updateChargeStatus(charge.id, 'failed', errorCode, errorMessage);
      logger.info(
        { requestId, chargeId, orderId: charge.order_id, errorCode },
        'Charge marked as failed'
      );
    } else {
      logger.debug(
        { requestId, chargeId },
        'Charge already failed, skipping update'
      );
    }
  } else {
    logger.warn(
      { requestId, chargeId, eventId: event.id },
      'Received payment.failed for unknown charge'
    );
  }
}

