// -----------------------------------------------------------------------------
// POST Handler
// -----------------------------------------------------------------------------

export async function POST(request: NextRequest) {
  // Step 1: Read raw body
  const bodyResult = await readRawBody(request, COMPONENT);
  if (!bodyResult.success) {
    return bodyResult.response;
  }
  const { rawBody, requestId } = bodyResult;

  try {
    // Step 2: Validate webhook secret is configured
    const webhookSecret = getWebhookSecret();
    if (!webhookSecret) {
      logger.error({ requestId }, 'SMF_WEBHOOK_SECRET is not configured');
      return createWebhookErrorResponse(
        'CONFIG_ERROR',
        'Webhook secret not configured',
        requestId,
        500
      );
    }

    // Step 3: Extract and validate signature headers
    const headersResult = extractSignatureHeaders(
      request.headers,
      'smf-signature',
      'smf-timestamp',
      requestId,
      COMPONENT
    );
    if (!headersResult.success) {
      return headersResult.response;
    }
    const { signature, timestamp } = headersResult;

    // Step 4: Verify webhook signature
    let event: SmfWebhookEvent;
    try {
      event = verifyWebhookSignature({
        payload: rawBody,
        signature,
        timestamp,
        secret: webhookSecret,
        toleranceSeconds: 300, // 5 minute tolerance for clock skew
      }) as SmfWebhookEvent;
    } catch (error) {
      if (error instanceof InvalidSignatureError) {
        logger.warn(
          { requestId, error: error.message },
          'Invalid webhook signature'
        );
        return createWebhookErrorResponse(
          'INVALID_SIGNATURE',
          'Invalid signature',
          requestId,
          401
        );
      }
      throw error;
    }

    // Log successful verification
    logWebhookReceived(COMPONENT, requestId, event.type, event.id);

    // Step 5: Check idempotency
    const idempotencyResult = await checkIdempotency({
      eventId: event.id,
      isProcessed: isWebhookEventProcessed,
      requestId,
      component: COMPONENT,
    });
    if (!idempotencyResult.success) {
      return idempotencyResult.response;
    }
    if (!idempotencyResult.isNewEvent) {
      return idempotencyResult.response!;
    }

    // Step 6: Store event record (with ON CONFLICT DO NOTHING for race safety)
    await createWebhookEvent({
      smf_event_id: event.id,
      event_type: event.type,
      payload: event as unknown as Record<string, unknown>,
      status: 'processing',
    });

    // Step 7: Process event based on type
    try {
      switch (event.type) {
        case 'payment.succeeded':
          await handlePaymentSucceeded(event, requestId);
          break;

        case 'payment.failed':
          await handlePaymentFailed(event, requestId);
          break;

        case 'refund.succeeded':
          await handleRefundSucceeded(event, requestId);
          break;

        case 'refund.failed':
          await handleRefundFailed(event, requestId);
          break;

        case 'payout.paid':
          await handlePayoutPaid(event, requestId);
          break;

        case 'payout.failed':
          await handlePayoutFailed(event, requestId);
          break;

        default:
          logUnhandledEventType(COMPONENT, requestId, event.type, event.id);
      }

      // Step 8: Mark event as processed
      await markWebhookEventProcessed(event.id);
      logWebhookProcessed(COMPONENT, requestId, event.type, event.id);

      return createWebhookSuccessResponse(requestId, event.id);

    } catch (processingError) {
      // Mark event as failed but still return 200 to prevent infinite retries
      const errorMessage = processingError instanceof Error
        ? processingError.message
        : 'Unknown processing error';

      await markWebhookEventProcessed(event.id, errorMessage);
      logWebhookFailed(COMPONENT, requestId, event.type, event.id, errorMessage);

      // Return 200 to acknowledge receipt (prevents infinite retries)
      // Change to 500 if you want SMF to retry failed events
      return createWebhookPartialResponse(requestId, errorMessage, event.id);
    }

  } catch (error) {
    const errorMessage = error instanceof Error ? error.message : 'Unknown error';
    logger.error(
      { requestId, error: errorMessage },
      'Webhook handler error'
    );

    return createWebhookErrorResponse(
      'INTERNAL_ERROR',
      'Webhook processing failed',
      requestId,
      500
    );
  }
}

// -----------------------------------------------------------------------------
// Note on Body Parsing
// -----------------------------------------------------------------------------
// In Next.js App Router, request.text() automatically provides the raw body.
// There's no need for the Pages Router `export const config = { api: { bodyParser: false } }`.
// The readRawBody() utility handles this correctly for App Router.
